'use client';
import React, { useState, useEffect, useRef } from 'react';
import { AlertDialog, AlertDialogContent, AlertDialogHeader, AlertDialogTitle, AlertDialogDescription, AlertDialogFooter } from '@/components/ui/alert-dialog';
import { Button } from '@/components/ui/button';
import Pokemon from '@/components/Pokemon';

const generateRandomPosition = () => ({
  x: Math.random() * 90 + 5,
  y: Math.random() * 90 + 5,
});

const SUCCESS_SOUND = "data:audio/wav;base64,";

const messages = [
  { text: "¬°Incre√≠ble captura! ‚ú®", gif: "https://media.giphy.com/media/2v170e71aanfi/giphy.gif" },
  { text: "¬°Wow, encontraste uno! üåü", gif: "https://media.giphy.com/media/l0MYt5jPR6QX5pnqM/giphy.gif" },
  { text: "¬°Excelente b√∫squeda! üéâ", gif: "https://media.giphy.com/media/3o6Zt481isNVuQI1l6/giphy.gif" },
  { text: "¬°Un Pok√©mon m√°s para tu colecci√≥n! üéÆ", gif: "https://media.giphy.com/media/3o7aD2saalBwwftBIY/giphy.gif" },
  { text: "¬°Sigue as√≠, entrenador! üèÜ", gif: "https://media.giphy.com/media/26ufdipQqU2lhNA4g/giphy.gif" }
];

const targetPokemons = [
  { id: 25, name: "Pikachu" },
  { id: 1, name: "Bulbasaur" },
  { id: 4, name: "Charmander" },
  { id: 7, name: "Squirtle" },
  { id: 133, name: "Eevee" }
];

export default function Home() {
  const [pokemons, setPokemons] = useState<Array<{ id: number; position: { x: number; y: number } }>>([]);
  const [message, setMessage] = useState<{ text: string; gif: string } | null>(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [score, setScore] = useState(0);
  const [bgColor, setBgColor] = useState('bg-white');
  const [targetPokemon, setTargetPokemon] = useState(targetPokemons[0]);
  const [difficulty, setDifficulty] = useState(10);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  const [loadedSavedProgress, setLoadedSavedProgress] = useState(false);

  // Load saved state from localStorage on mount or difficulty change
  useEffect(() => {
    const savedScore = localStorage.getItem(`score_${difficulty}`);
    const savedTargetPokemon = localStorage.getItem(`targetPokemon_${difficulty}`);
    const savedPokemons = localStorage.getItem(`pokemons_${difficulty}`);

    if (savedScore && savedTargetPokemon && savedPokemons) {
      setScore(parseInt(savedScore));
      setTargetPokemon(JSON.parse(savedTargetPokemon));
      setPokemons(JSON.parse(savedPokemons));
      setLoadedSavedProgress(true);
    } else {
      setScore(0);
      setTargetPokemon(targetPokemons[Math.floor(Math.random() * targetPokemons.length)]);
      const initialPokemons = Array.from({ length: difficulty }, (_, index) => ({
        id: (index % 150) + 1,
        position: generateRandomPosition(),
      }));
      setPokemons(initialPokemons);
      setLoadedSavedProgress(false);
    }
  }, [difficulty]);

  // Save state to localStorage on changes
  useEffect(() => {
    localStorage.setItem(`score_${difficulty}`, score.toString());
    localStorage.setItem(`targetPokemon_${difficulty}`, JSON.stringify(targetPokemon));
    localStorage.setItem(`pokemons_${difficulty}`, JSON.stringify(pokemons));
  }, [score, difficulty, targetPokemon, pokemons]);

  useEffect(() => {
    if (loadedSavedProgress) {
      // Skip resetting if loaded saved progress
      setLoadedSavedProgress(false);
      return;
    }
    const initialPokemons = Array.from({ length: difficulty }, (_, index) => ({
      id: (index % 150) + 1,
      position: generateRandomPosition(),
    }));
    setPokemons(initialPokemons);
    setTargetPokemon(targetPokemons[Math.floor(Math.random() * targetPokemons.length)]);

    // Add keydown event listener for 'y' key to catch the current target Pokemon
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key.toLowerCase() === 'y') {
        handlePokemonCatch(targetPokemon.id);
      }
    };
    window.addEventListener('keydown', handleKeyDown);

    return () => {
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [difficulty]);


  const handlePokemonCatch = (pokemonId: number) => {
    try {
      if (pokemonId === targetPokemon.id) {
        setPokemons(prev => prev.filter(p => p.id !== pokemonId));
        setScore(prev => prev + 1);
        const newMessage = messages[Math.floor(Math.random() * messages.length)];
        setMessage(newMessage);
        setIsDialogOpen(true);
        
        if (audioRef.current) {
          audioRef.current.src = SUCCESS_SOUND;
          audioRef.current.play().catch(e => console.log('Audio play failed:', e));
        }
        
        const remainingTargets = targetPokemons.filter(p => p.id !== targetPokemon.id);
        if (remainingTargets.length > 0) {
          setTargetPokemon(remainingTargets[Math.floor(Math.random() * remainingTargets.length)]);
        }
        
        const colors = ['bg-blue-50', 'bg-green-50', 'bg-yellow-50', 'bg-pink-50', 'bg-purple-50'];
        const newColor = colors[Math.floor(Math.random() * colors.length)];
        setBgColor(newColor);
      }
    } catch (error) {
      console.error("Error handling Pok√©mon catch:", error);
    }
  };

  const skipTarget = () => {
    const remainingTargets = targetPokemons.filter(p => p.id !== targetPokemon.id);
    if (remainingTargets.length > 0) {
      setTargetPokemon(remainingTargets[Math.floor(Math.random() * remainingTargets.length)]);
    }
  };

  return (
    <main className={`min-h-screen ${bgColor} transition-colors duration-500 relative overflow-hidden cursor-crosshair bg-[url('https://blogger.googleusercontent.com/img/b/R29vZ2xl/AVvXsEjfzr-WCdKgiSpWRMCDItaxlhQgzWbFarIjCQTiZbM21OOBaHpXA9ce-9FBqQ6K5JvLvTe0hYmrhh_QmRfmrrchEQ8VxAOelt-wotlfl7ipof6gnggW83IObm9FB0iRb1FmqtyfSs4zVF83gv-7ghjwETrZSLRk7g-dM3vDJJn7waYZy_C1PdvpSuESpg/s1920/Ash_Ketchum_World_Champion_Screenshot_4.jpg')] bg-cover bg-center`}>
      <audio ref={audioRef} />
      <div className="fixed top-4 right-4 bg-black text-white px-4 py-2 rounded-full shadow-lg font-sunshiney text-xl flex flex-col items-center gap-2">
        <div>Pok√©mon Encontrados: {score}/{difficulty}</div>
        <div className="flex gap-2">
          {[10, 50, 200].map((level) => (
            <button
              key={level}
              onClick={() => {
                setDifficulty(level);
                setScore(0);
                setMessage(null);
                setIsDialogOpen(false);
                setBgColor('bg-white');
              }}
              className={`px-2 py-1 rounded font-bold ${difficulty === level ? 'bg-blue-600 text-white' : 'bg-gray-700 text-white'}`}
            >
              {level}
            </button>
          ))}
        </div>
      </div>

      <div className="text-center py-8">
        <div className="bg-white mx-auto max-w-3xl rounded-xl p-6 shadow-lg flex flex-col md:flex-row items-center justify-between gap-4">
          <div>
            <h1 className="text-4xl md:text-5xl mb-2 text-blue-900 drop-shadow-lg">
              ¬°Encuentra los Pok√©mon! 
            </h1>
            <p className="text-lg text-black mb-2">
              Encuentra a <span className="text-blue-900 font-bold">{targetPokemon.name}</span>
            </p>
            <p className="text-sm text-gray-700 font-sunshiney">
              ¬°Busca entre los Pok√©mon escondidos en la pantalla!
            </p>
            <div className="mt-4 flex gap-4 justify-center flex-wrap">
              <button
                onClick={() => {
                  setDifficulty(10);
                  setScore(0);
                  setMessage(null);
                  setIsDialogOpen(false);
                  setBgColor('bg-white');
                }}
                className={`px-4 py-2 rounded-lg font-bold ${difficulty === 10 ? 'bg-blue-600 text-white' : 'bg-gray-200 text-black'}`}
              >
                F√°cil (10)
              </button>
              <button
                onClick={() => {
                  setDifficulty(50);
                  setScore(0);
                  setMessage(null);
                  setIsDialogOpen(false);
                  setBgColor('bg-white');
                }}
                className={`px-4 py-2 rounded-lg font-bold ${difficulty === 50 ? 'bg-blue-600 text-white' : 'bg-gray-200 text-black'}`}
              >
                Medio (50)
              </button>
              <button
                onClick={() => {
                  setDifficulty(200);
                  setScore(0);
                  setMessage(null);
                  setIsDialogOpen(false);
                  setBgColor('bg-white');
                }}
                className={`px-4 py-2 rounded-lg font-bold ${difficulty === 200 ? 'bg-blue-600 text-white' : 'bg-gray-200 text-black'}`}
              >
                Dif√≠cil (200)
              </button>
              <button
                onClick={() => {
                  setScore(0);
                  setMessage(null);
                  setIsDialogOpen(false);
                  setBgColor('bg-white');
                  setPokemons([]);
                  localStorage.clear();
                }}
                className="px-4 py-2 rounded-lg font-bold bg-red-600 text-white"
              >
                Reiniciar
              </button>
              <button
                onClick={() => {
                  console.log('Saving progress...');
                  localStorage.setItem('score', score.toString());
                  localStorage.setItem('difficulty', difficulty.toString());
                  localStorage.setItem('targetPokemon', JSON.stringify(targetPokemon));
                  localStorage.setItem('pokemons', JSON.stringify(pokemons));
                  alert('Progreso guardado');
                }}
                className="px-4 py-2 rounded-lg font-bold bg-green-600 text-white"
              >
                Guardar Progreso
              </button>
              <button
                onClick={() => {
                  console.log('Loading progress...');
                  const savedScore = localStorage.getItem('score');
                  const savedDifficulty = localStorage.getItem('difficulty');
                  const savedTargetPokemon = localStorage.getItem('targetPokemon');
                  const savedPokemons = localStorage.getItem('pokemons');

                  if (savedScore) setScore(parseInt(savedScore));
                  if (savedDifficulty) setDifficulty(parseInt(savedDifficulty));
                  if (savedTargetPokemon) setTargetPokemon(JSON.parse(savedTargetPokemon));
                  if (savedPokemons) setPokemons(JSON.parse(savedPokemons));
                  alert('Progreso cargado');
                }}
                className="px-4 py-2 rounded-lg font-bold bg-yellow-600 text-white"
              >
                Cargar Progreso
              </button>
            </div>
          </div>
          <Button onClick={skipTarget} className="bg-yellow-400 text-black font-sunshiney text-xl px-4 py-2 rounded-lg shadow-md hover:bg-yellow-300 transition">
            Saltar Personaje
          </Button>
        </div>
      </div>

      <div className="relative w-full h-[80vh]">
        {pokemons.map((pokemon) => (
          <Pokemon
            key={`${pokemon.id}-${pokemon.position.x}-${pokemon.position.y}`}
            id={pokemon.id}
            position={pokemon.position}
            onCatch={() => handlePokemonCatch(pokemon.id)}
          />
        ))}
      </div>

      <AlertDialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <AlertDialogContent className="bg-white">
          <AlertDialogHeader>
            <AlertDialogTitle className="text-2xl font-sunshiney">¬°{targetPokemon.name} Encontrado!</AlertDialogTitle>
            <AlertDialogDescription asChild>
              <div className="text-xl py-4 flex flex-col items-center font-sunshiney">
                <div>{message?.text}</div>
                {message && <img src={message.gif} alt="Pok√©mon GIF" className="mt-4 w-48 h-48 object-contain" />}
              </div>
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <Button 
              onClick={() => setIsDialogOpen(false)}
              className="bg-black hover:bg-gray-800 text-white font-sunshiney text-xl"
            >
              ¬°Seguir Buscando!
            </Button>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      <AlertDialog open={score === difficulty} onOpenChange={() => {}}>
        <AlertDialogContent className="bg-white">
          <AlertDialogHeader>
            <AlertDialogTitle className="text-2xl font-sunshiney">üèÜ ¬°Victoria!</AlertDialogTitle>
            <AlertDialogDescription asChild>
              <div className="text-xl py-4 font-sunshiney flex flex-col items-center gap-4">
                ¬°Felicitaciones! Has encontrado todos los Pok√©mon objetivo. ¬°Eres un verdadero Maestro Pok√©mon!
                <img src="https://media.giphy.com/media/26ufdipQqU2lhNA4g/giphy.gif" alt="Victory Celebration" className="w-48 h-48 object-contain" />
              </div>
            </AlertDialogDescription>
          </AlertDialogHeader>
        </AlertDialogContent>
      </AlertDialog>
    </main>
  );
}
